
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import XImg from 'vux/src/components/x-img/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import Scroll from "@/components/scroll";
import {backMixin} from "../../common/js/picBack";
import jsonp from 'jsonp';
import { mapGetters, mapMutations } from "vuex";

export default {
name: "Check",
components: {
XHeader,
Scroll,
Divider,
XButton,
Group,
Cell,
XImg,
Previewer,
Actionsheet,
XInput,XDialog,Checklist
},
mixins:[backMixin],

mounted() {
if (this.$route.query.backIndex && this.$route.query.backIndex == 1){
document.addEventListener("plusready",()=>{
this.getLocation();
plus.key.removeEventListener("backbutton", () => {});
plus.key.addEventListener("backbutton", () => {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
});
});
}else{
this.getLocation();
}
this.$bus.on("selectedClient", val => {
this.formData.CustomerID = val.ID;
this.formData.CustomerName = val.CustomerName;
});





},
data() {
return {
showDialog:false,
searchCompList: [{key: '1', value: '001 value'}, {key: '2', value: '002 value'}, {key: '3', value: '003 value'}],

showAction:false,
actionMenu:{
shot: '拍照',
},
buttonAble:false,
options: {
saveImg(el) {
console.log(el);
}
},
pics: [],
formData: {
ID: 0,
SigninTime: dateFormat(new Date(), "YYYY-MM-DD HH:mm:ss"),
CustomerID: 0,
CustomerName: "",
Province: "",
City: "",
District: "",
Street: "",
Address: "",
MapLng: 0,
MapLat: 0,
Remark: "",
ImageAttach: []
},
isSelectCust: true
};
},
computed: {
...mapGetters(["getSelectCustomer"]),
maxPic() {
return 9 - this.formData.ImageAttach.length;
}
},
methods: {
...mapMutations({
setSelectCustomer:"setSelectCustomer"
}),
back(){
if (this.$route.query.backIndex && this.$route.query.backIndex == 1) {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
} else {
this.$router.back();
}
},

showImage(idx) {
this.$refs.previewer.show(idx);
},
returnFalse() {
return false;
},
setMap(position) {

if (position) {
this.formData.Province = position.address.province;
this.formData.City = position.address.city;
this.formData.District = position.address.district;
this.formData.Street = position.address.street;
this.formData.Address = position.address.province+position.address.city+position.address.district+position.address.address;
this.formData.MapLng = position.coords.longitude;
this.formData.MapLat = position.coords.latitude;
}

let map = new BMap.Map("container");
let point = new BMap.Point(this.formData.MapLng, this.formData.MapLat);
let marker = new BMap.Marker(point); 
var circle = new BMap.Circle(point, 500, {
strokeColor: "red",
strokeWeight: 1,
strokeOpacity: 0.3
}); //创建圆
map.addOverlay(marker); 
map.addOverlay(circle);
map.centerAndZoom(point, 15);
},
selectCustomer() {
if (!this.isSelectCust) return false;
this.$router.push({
path: "/check/selectCust"
});
},

geoLngLatToBaidu(lng,lat,coordtype){
let baiduAK = "W6WIQmkWMM7fHZ1bQzONDGwimUikfaPU";
let from = coordtype == "gcj02" ? 3 : 1;
let url = "http://api.map.baidu.com/geoconv/v1/?coords=" + lng + ',' + lat + "&output=json&from=" + from + "&to=5&ak=" + baiduAK;
return new Promise((resolve, reject)=>{
jsonp(url, null, (err, res) => {
if (err) {
console.error(err.message);
} else {
if(res && res.status == 0) {
resolve({
lng: res.result[0].x,
lat: res.result[0].y
})
}
}
})
})
},
geoDeCoder(_lat, _lng, _coordtype){
var baiduAK = "W6WIQmkWMM7fHZ1bQzONDGwimUikfaPU";
var type = "wgs84";
var url = 'http://api.map.baidu.com/geocoder/v2/?coordtype=' + type + '&location=' + _lat + ',' + _lng + '&latest_admin=1&output=json&ak=' + baiduAK;
return new Promise((resolve, reject)=>{
jsonp(url, null, (err, res) => {
if (err) {
console.error(err.message);
} else {
if(res && res.status == 0) {
resolve( {
country: res.result.addressComponent.country || "",
province: res.result.addressComponent.province || "",
city: res.result.addressComponent.city || "",
district: res.result.addressComponent.district || "",
address: res.result.addressComponent.street || "",
street: res.result.sematic_description || ""
})
}
}
})
})
},
getLocation() {
let options = {}
let _this = this;
let location = {coords:{},address:{}}
if(plus.os.name == 'iOS'){
plus.geolocation.getCurrentPosition(
res => {
_this.geoLngLatToBaidu(res.coords.longitude, res.coords.latitude, res.coordsType)
.then(_res=>{
location.coords.longitude = _res.lng
location.coords.latitude = _res.lat
_this.geoDeCoder(_res.lat,_res.lng)
.then(__res =>{
location.address = __res
_this.setMap(location);
}).catch(err=>{
console.log(JSON.stringify(location))
})
})

},
function(e) {
alert("定位失败，请检查网络");
console.log(JSON.stringify(e));
},
);
}else{
plus.geolocation.getCurrentPosition(
res => {
res.address.address = res.address.street
_this.setMap(res)
},
function(e) {
alert("定位失败，请检查网络");
console.log(JSON.stringify(e));
},
{
provider: "baidu",
geocode: true,
coordsType: "bd09ll",
}
);
}

},
getLocationBd(){
let _this = this;
let geolocation = new BMap.Geolocation();
geolocation.getCurrentPosition(function(res){
res.coords = {}
res.coords.longitude = res.longitude
res.coords.latitude = res.latitude
_this.setMap(res)
})
},
reLocation() {
this.getLocation();
},
startSelectPic(){
this.showAction=true
},
chooseSelectType(tid){
if (this.maxPic <= 0) {
this.$vux.toast.show({
text:"最多可选9张图片",
position: "middle",
type: "cancel"
})
return;
}
if(tid == 'shot'){
this.shotAndChoose()
}else if(tid=='album'){
this.addPic()
}else{

}
},
shotAndChoose(){
if (this.maxPic <= 0) {
this.$vux.toast.show({
text:"最多可选9张图片",
position: "middle",
type: "cancel"
})
return;
}
const camera = plus.camera.getCamera();
camera.captureImage(path => {
if(path){
this.selectPic([path])
}
});
},
addPic() {
let map = document.querySelector(".checkPage")
map.scrollTop = 360;

let _this = this;
if (this.maxPic <= 0) {
this.$vux.toast.show({
text:"最多可选9张图片",
position: "middle",
type: "cancel"
})
return;
}
plus.gallery.pick(
function(pics) {
_this.selectPic(pics.files);
},
function(e) {
console.log("取消选择图片");
},
{
filter: "image",
multiple: true,
maximum: _this.maxPic,
system: false
}
);
},
selectPic(pics) {
this.uploadPic(pics);
},
uploadPic(pics) {
let _this = this;
let _server = this.BillFileUrl(1);
let wt = plus.nativeUI.showWaiting();
wt.close();
let task = plus.uploader.createUpload(
_server,
{ method: "POST" },
function(t, status) {

if (status == 200) {
_this.$vux.loading.hide()
wt.close();
let res = JSON.parse(t.responseText);

if (res.State == 1) {
res.Data.forEach(item => {
item.PicPath = `${api50}/api/v1/File/DownLoadPIc?filePath=${
item.FilePath
}`;
_this.pics.push({src:item.PicPath})
});
} else {
_this.$vux.toast.show({
text: res.ErrorMessage,
position: "middle",
type: "cancel"
});
return false;
}
_this.formData.ImageAttach.push(...res.Data);
} else {
alert("上传失败！");
}
}
);
let _promiseList = []
for (let i = 0; i < pics.length; i++) {
let pic = pics[i]
_promiseList.push(this.zipPic(pic, i))
}

let index = 0;
task.addEventListener(
"statechanged",
(upload, status) => {
if (upload.totalSize > 1024 * 1024 * 10) {
this.$vux.toast.show({
type: "warn",
text: "图片大小不超过10M"
});
task.abort();
return false;
} else {
index++;
}
},
false
);

Promise.all(_promiseList).then((images)=>{
for(let i=0;i<images.length;i++){
task.addFile(images[i].path, { key: images[i].name});
}
}).then(()=>{
task.start();
})

},
zipPic(pic,idx){
return new Promise((resolve, reject)=>{
let reg = new RegExp('(.{' + pic.lastIndexOf('.') + '})');
var zipFile = pic.replace(reg, '$1_zip');
var zipExt = zipFile.substr(zipFile.lastIndexOf('.') + 1, zipFile.length - zipFile.lastIndexOf('.'));
var zipPath = zipFile.substr(0, zipFile.lastIndexOf('/') + 1) + idx + "." + zipExt;
let fileName = zipPath.substr(zipPath.lastIndexOf('/') + 1, zipPath.length - zipPath.lastIndexOf('/'));
plus.zip.compressImage({
src: pic,
dst: zipPath,
overwrite: true,
width: "50%",
quality: 60
}, function(e) {
resolve({path:e.target, name:fileName})
}, function(e) {
reject(e)
});
})
},
removePic(idx) {
this.formData.ImageAttach.splice(idx, 1);
this.pics.splice(idx,1)
},

beforeCheckIn(){
if(this.formData.CustomerID !=0){
this.checkIn()
}else{
if(!this.formData.CustomerName){
this.checkIn()
}else{
let _url = "/Customer/GetCompCustomerList"
this.getAxios(_url, {page:1, count:10, customerName:this.formData.CustomerName}).then(
res=>{
if(res.Data.List.length > 0){
let _list = res.Data.List;
this.searchCompList = _list.map(ele=>{return {key:ele.ID, value:ele.CustomerName}})
this.showDialog = true;
}else{
this.checkIn()
}
}
)
}

}
},
selectedCust(value, label){
if(value.length >0 && label.length >0 ){
this.showDialog = false;
this.formData.CustomerID = value[0]
this.formData.CustomerName = label[0]
this.checkIn();
}
},
checkIn() {
let _this = this;
_this.showDialog = false;
if (!this.formData.MapLng && !this.formData.Address) {
this.$vux.toast.show({
text: "请先进行定位",
position: "middle",
type: "warn"
});
return false;
}
if (!this.formData.Address) {
this.$vux.toast.show({
text: "请先定位获取地址",
position: "middle",
type: "warn"
});
return false;
}












let _url = "/Work/SaveCrmWorkSignin";
let _data = this.formData;

_data.ImageAttach.map(item => {
item.ID = 0;
item.FileSize = 100;
item.FileType = 1;
});
this.buttonAble = true;
this.postAxios(_url, _data)
.then(res => {
if (res.State == 1) {
this.$vux.toast.show({
text: "记录成功！",
position: "middle",
type: "success",
onHide() {
_this.setSelectCustomer({ID:0,CustomerName:''});
_this.back();
}
});
}
})
.catch(e => {
this.buttonAble = false;
this.$vux.toast.show({
text: e.ErrorMessage,
position: "middle",
type: "cancel",
onHide() {
_this.buttonAble = false;
}
});
console.log(JSON.stringify(e));
});
}
}
};



